@import "./../utils/variables";
@import "./../utils/mixins";

.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    cursor: pointer;
    @include card-size;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 48px;
    transition: all $time-hover;

    &:hover {
      transform: scale(1.2);
      color: $hover-card-color;
    }

    &:not(:nth-child(4n)) {
      margin-right: 48px;
    }
  }
}
